import classNames from 'classnames';
import Cell from 'components/Cell';
import cellStyles from 'components/Cell/cell.module.scss';
import MinesweeperContext from 'contexts/minesweeper';
import useCallbackRef from 'hooks/use-callback-ref';
import React, { SyntheticEvent, useContext } from 'react';

import styles from './mine-field.module.scss';

const MineField = () => {
  const {
    mineFieldParams: { rowsCount },
    mineField,
    mineFieldStatus: { isSetup, isBusted, isSolved },
    setupMineField,
    revealCell,
    toggleFlag,
    revealNeighbors,
  } = useContext(MinesweeperContext);

  const handleContextMenu = (event: SyntheticEvent<HTMLDivElement>) => {
    event.preventDefault();
  };

  const handleCellClick = useCallbackRef((cell: Cell) => {
    (isSetup ? revealCell : setupMineField)(cell);
  });

  const handleCellMouseDown = useCallbackRef(
    (
      { target, nativeEvent }: SyntheticEvent<HTMLButtonElement>,
      cell: Cell
    ) => {
      const handleMouseUp = (event: Event) => {
        if ((nativeEvent as MouseEvent).which !== (event as MouseEvent).which) {
          revealNeighbors(cell);
        }

        target.removeEventListener('mouseup', handleMouseUp);
      };

      target.addEventListener('mouseup', handleMouseUp);
    }
  );

  const handleCellContextMenu = useCallbackRef((cell: Cell) => {
    toggleFlag(cell);
  });

  return (
    <div
      className={styles.mine_field}
      style={{ width: rowsCount * parseFloat(cellStyles.cellSize) }}
      onContextMenu={handleContextMenu}
    >
      <div
        className={classNames(styles[`row_${rowsCount}`], {
          [styles.disabled]: isBusted || isSolved,
        })}
      >
        {mineField.map((cell) => (
          <Cell
            cell={cell}
            onClick={handleCellClick}
            onMouseDown={handleCellMouseDown}
            onContextMenu={handleCellContextMenu}
            key={cell.id}
          />
        ))}
      </div>
    </div>
  );
};

export default MineField;
