import React, { memo } from 'react';

interface Props<T> {
  item: T;
  name: string;
  checked?: boolean;
  className?: string;
  onChange?: (item: T) => void;
}

const Radio = <T extends { name: string }>({
  item,
  name,
  checked,
  className,
  onChange,
}: Props<T>) => {
  const handleChange = () => {
    onChange?.(item);
  };

  return (
    <>
      <input
        type="radio"
        name={name}
        className={className}
        defaultChecked={checked}
        onChange={handleChange}
      />
      {item.name}
    </>
  );
};

export default memo(Radio) as typeof Radio;
