import CellChecker from 'utils/cell-checker';
import IndexUtils from 'utils/index-utils';

const isValidArrayIndex = (index: number, length: number) =>
  index > -1 && index < length;

class NeighborsUtils {
  constructor(
    private rowsCount: number,
    private columnsCount: number,
    private indexUtils = new IndexUtils(rowsCount)
  ) {}

  getIndexes({ index }: Cell) {
    const { rowIndex, columnIndex } = this.indexUtils.to2D(index);
    const indexes = [];

    for (let rowIndexOffset = -1; rowIndexOffset < 2; rowIndexOffset += 1) {
      for (
        let columnIndexOffset = -1;
        columnIndexOffset < 2;
        columnIndexOffset += 1
      ) {
        if (rowIndexOffset || columnIndexOffset) {
          const targetRowIndex = rowIndex + rowIndexOffset;
          const targetColumnIndex = columnIndex + columnIndexOffset;

          if (
            isValidArrayIndex(targetRowIndex, this.rowsCount) &&
            isValidArrayIndex(targetColumnIndex, this.columnsCount)
          ) {
            indexes.push(
              this.indexUtils.to1D(targetRowIndex, targetColumnIndex)
            );
          }
        }
      }
    }

    return indexes;
  }

  canFloodFill(mineField: Cell[], cell: Cell) {
    return !this.getIndexes(cell).some((index) => {
      const { isMined, isFlagged } = new CellChecker(mineField[index]);

      return isMined && !isFlagged;
    });
  }

  canReveal(mineField: Cell[], cell: Cell) {
    return (
      this.countMined(mineField, cell) ===
      this.countBy(mineField, cell, 'isFlagged')
    );
  }

  countMined(mineField: Cell[], cell: Cell) {
    return this.countBy(mineField, cell, 'isMined');
  }

  private countBy(mineField: Cell[], cell: Cell, key: keyof CellChecker) {
    return this.getIndexes(cell).reduce(
      (acc, index) => (new CellChecker(mineField[index])[key] ? acc + 1 : acc),
      0
    );
  }
}

export default NeighborsUtils;
