import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { memo } from 'react';

import styles from './face.module.scss';

interface Props extends Pick<MineFieldStatus, 'isSolved' | 'isBusted'> {
  onClick?: () => void;
}

const Face = ({ onClick, isSolved, isBusted }: Props) => {
  let icon = <FontAwesomeIcon icon={['far', 'meh']} />;

  if (isBusted) {
    icon = <FontAwesomeIcon icon={['far', 'frown']} />;
  }

  if (isSolved) {
    icon = <FontAwesomeIcon icon={['far', 'smile']} />;
  }

  return (
    <button type="button" className={styles.face} onClick={onClick}>
      {icon}
    </button>
  );
};

export default memo(Face);
