import CellAppearance from 'constants/cell-appearance';
import CellValue from 'constants/cell-value';
import { smallMineFieldParams } from 'constants/mine-fields-params';
import useDidUpdate from 'hooks/use-did-update';
import produce, { Draft } from 'immer';
import { difference, range } from 'lodash-es';
import { useMemo, useState } from 'react';
import addId from 'utils/add-id';
import CellChecker from 'utils/cell-checker';
import MineFieldChecker from 'utils/mine-field-checker';
import NeighborsUtils from 'utils/neighbors-utils';

type DraftCallback = (draft: Draft<Cell[]>) => void;

const useMineField = () => {
  const [mineFieldParams, setMineFieldParams] = useState(smallMineFieldParams);

  const { rowsCount, columnsCount, minesCount } = mineFieldParams;
  const mineFieldLength = rowsCount * columnsCount;

  const emptyMineField = useMemo(
    () =>
      Array.from({ length: mineFieldLength }, (item, index) =>
        addId({
          index,
          value: CellValue.Empty,
          appearance: CellAppearance.Hidden,
        })
      ),
    [mineFieldLength]
  );

  const [mineField, setMineField] = useState(emptyMineField);

  const initialMineFieldStatus = {
    isSetup: false,
    isBusted: false,
    isSolved: false,
    hiddenMinesCount: minesCount,
  };

  const [mineFieldStatus, setMineFieldStatus] = useState(
    initialMineFieldStatus
  );

  const neighborsUtils = new NeighborsUtils(rowsCount, columnsCount);

  const getFloodFilledMineField = (cell: Cell, draftCallback?: DraftCallback) =>
    produce(mineField, (draft) => {
      draftCallback?.(draft);
      /* eslint-disable no-param-reassign */
      draft[cell.index].appearance = CellAppearance.Visible;

      (function floodFill(item: Cell) {
        if (neighborsUtils.canFloodFill(draft, item)) {
          neighborsUtils.getIndexes(item).forEach((index) => {
            const neighbor = draft[index];
            const { isMined, isHidden, isFlagged } = new CellChecker(neighbor);

            if (!isMined && isHidden && !isFlagged) {
              neighbor.appearance = CellAppearance.Visible;

              floodFill(neighbor);
            }
          });
        }
      })(cell);
    });

  const getBustedMineField = (cell: Cell, draftCallback: DraftCallback) =>
    produce(mineField, (draft) => {
      draftCallback(draft);
      draft.forEach((item) => {
        const {
          isMisplacedFlag,
          isMined,
          isBusted,
          isFlagged,
        } = new CellChecker(item);

        if (isMisplacedFlag) {
          item.appearance = CellAppearance.Wrong;
        } else if (isMined && !isBusted && !isFlagged) {
          item.appearance = CellAppearance.Visible;
        }
      });
    });

  const setupMineField = (cell: Cell) => {
    setMineField(
      getFloodFilledMineField(cell, (draft) => {
        const restIndexes = difference(range(mineFieldLength), [
          cell.index,
          ...neighborsUtils.getIndexes(cell),
        ]);
        const minedIndexes = new Set<number>();

        while (minedIndexes.size < minesCount) {
          minedIndexes.add(
            restIndexes[Math.floor(Math.random() * restIndexes.length)]
          );
        }

        minedIndexes.forEach((index) => {
          draft[index].value = CellValue.Mine;
        });

        draft.forEach((item) => {
          if (!new CellChecker(item).isMined) {
            item.value = neighborsUtils.countMined(draft, item);
          }
        });
      })
    );

    setMineFieldStatus((prevMineFieldStatus) => ({
      ...prevMineFieldStatus,
      isSetup: true,
    }));
  };

  const revealCell = (cell: Cell) => {
    setMineField(
      new CellChecker(cell).isMined
        ? getBustedMineField(cell, (draft) => {
            draft[cell.index].appearance = CellAppearance.Busted;
          })
        : getFloodFilledMineField(cell)
    );
  };

  const toggleFlag = (cell: Cell) => {
    const { isFlagged } = new CellChecker(cell);

    setMineField((prevMineField) =>
      produce(prevMineField, (draft) => {
        draft[cell.index].appearance = isFlagged
          ? CellAppearance.Hidden
          : CellAppearance.Flagged;
      })
    );

    setMineFieldStatus((prevMineFieldStatus) =>
      produce(prevMineFieldStatus, (draft) => {
        draft.hiddenMinesCount += isFlagged ? 1 : -1;
      })
    );
  };

  const revealNeighbors = (cell: Cell) => {
    if (neighborsUtils.canFloodFill(mineField, cell)) {
      setMineField(getFloodFilledMineField(cell));
    } else if (neighborsUtils.canReveal(mineField, cell)) {
      setMineField(
        getBustedMineField(cell, (draft) => {
          neighborsUtils.getIndexes(cell).forEach((index) => {
            const neighbor = draft[index];
            const { isMined, isFlagged, isMisplacedFlag } = new CellChecker(
              neighbor
            );

            if (isMined && !isFlagged) {
              neighbor.appearance = CellAppearance.Busted;
            } else if (!isMisplacedFlag) {
              neighbor.appearance = CellAppearance.Visible;
            }
          });
        })
      );
    }
  };

  const reset = () => {
    setMineField(emptyMineField);
    setMineFieldStatus(initialMineFieldStatus);
  };

  useDidUpdate(() => {
    reset();
  }, emptyMineField);

  useDidUpdate(() => {
    const mineFieldChecker = new MineFieldChecker(mineField);

    if (mineFieldChecker.isBusted()) {
      setMineFieldStatus((prevMineFieldStatus) => ({
        ...prevMineFieldStatus,
        isBusted: true,
      }));
    } else if (mineFieldChecker.isSolved()) {
      setMineField((prevMineField) =>
        produce(prevMineField, (draft) => {
          draft.forEach((cell) => {
            if (new CellChecker(cell).isMined) {
              cell.appearance = CellAppearance.Flagged;
            }
          });
        })
      );

      setMineFieldStatus((prevMineFieldStatus) => ({
        ...prevMineFieldStatus,
        isSolved: true,
        hiddenMinesCount: 0,
      }));
    }
  }, mineField);

  return {
    mineField,
    mineFieldParams,
    mineFieldStatus,
    setMineFieldParams,
    setupMineField,
    revealCell,
    toggleFlag,
    revealNeighbors,
    reset,
  };
};

export default useMineField;
