class IndexUtils {
  constructor(private length: number) {}

  to2D(index: number) {
    const rowIndex = index % this.length;

    return { rowIndex, columnIndex: (index - rowIndex) / this.length };
  }

  to1D(rowIndex: number, columnIndex: number) {
    return columnIndex * this.length + rowIndex;
  }
}

export default IndexUtils;
